var React = require('react')
var Link = require('react-router').Link

var Category = require('./category.jsx')
var JobPanelActions = require('../actions/job-panel-actions.js')


module.exports = React.createClass({
	render: function() {
	
		return (
			<ul className="nav nav-pills nav-stacked" >
			<li onClick={this.selectCategory} className={this.props.selectedClass ? 'active' : ''}><a className="btn" >Home</a></li>
			{this.renderCategory()}
			</ul>
			)
	},
	renderCategory: function(){
		var selectedClass = this.props.selectedClass
		return this.props.items.map(function(category){
			return <Category item={category} key={category.id} />
		})
	},

	selectCategory: function(){
		//console.log(this.props.item.id)
		JobPanelActions.selectCategory()
	}

})


