var React = require('react')
var ReactDom = require('react-dom')

var CategoryActions = require('../actions/category-actions')
var CategoryStore = require('../stores/category-store')

module.exports = React.createClass({
	getInitialState: function() {
		return CategoryStore.getState()
	},

	componentWillMount: function() {
		CategoryStore.listen(this.onChange)
	},
	componentWillUnmount: function() {
		CategoryStore.unlisten(this.onChange)
	},
	render: function() {
		var created = this.state.created
		return (
			<div className="container">
			{created ? this.renderSuccess() : ""}
			<form  onSubmit={this.handleSubmit}> 
			
			
			<input type="text" className="form-control" placeholder="Enter Name"  ref="name"/> 
			
		  	  
			<div></div>
			<button type="submit" className="btn btn-success">Submit</button>
			</form>
			</div>

			)
	},
	onChange: function(state) {
		this.setState(state)
	},

	handleSubmit: function(e) {
		e.preventDefault()
		var name = ReactDom.findDOMNode(this.refs.name).value.trim()
		if(!name){
			return
		}

		var postdata ={}
	 postdata.name = name
 	
   	CategoryActions.create(postdata)

		ReactDom.findDOMNode(this.refs.name).value='';

		return;

	},
	renderSuccess: function() {
 		return <div className="alert alert-success">Success! Well done category created.</div>
		
	}
})


