var React = require('react')

var JobPanelAction = require('../actions/job-panel-actions.js')
var JobPanelStore = require('../stores/job-panel-store.js')
var JobsList = require('./jobs-list.jsx')
var CategoryPanel = require('./category-panel.jsx')

module.exports = React.createClass({
	getInitialState: function(){
		return JobPanelStore.getState()
	},
	componentWillMount: function(){
		JobPanelStore.listen(this.onChange)
	},
	componentWillUnmount: function(){
		JobPanelStore.unlisten(this.onChange)
	},
	onChange: function(state){
		this.setState(state)
	},
	render: function(){
		var selectedCategory = this.state.selectedCategory
		var selectedClass = this.state.selectedClass
		
		//console.log(activeClassName)
		return (
			<div className="row">
			<CategoryPanel  selectedClass = {selectedClass}/>
			<JobsList selectCategory={selectedCategory}  />
			</div>
			)
	}
})


