var React = require('react')

var CategoryStore = require('../stores/category-store.js')
var CategoryActions = require('../actions/category-actions.js')
var CategoryList = require('./category-list.jsx')

module.exports = React.createClass({
	getInitialState: function(){
		return CategoryStore.getState()
	},
	componentWillMount: function() {
		CategoryStore.listen(this.onChange)
		CategoryActions.load()
	},
	componentWillUnmount: function() {
		CategoryStore.unlisten(this.onChange)
	},
	render: function() {
		var categories = this.state.categories
		return(
			<div className="col-md-3">
			<CategoryList items={categories} selectedClass={this.props.selectedClass} />
			</div>
			)
	},
	onChange: function(state) {
		this.setState(state)
	}
})


