
var alt = require('../libs/alt-application.js')



var JobPanelActions = require('../actions/job-panel-actions.js')

module.exports = alt.createStore({
  displayName: 'JobPanelStore',

  bindListeners: {
    selectCategory: JobPanelActions.selectCategory
    
  },

  state: {
   selectedCategory: null,
   selectedClass: ''
   
  },
  
  selectCategory: function(item){
    if (item) 
      this.setState({selectedCategory: item, selectedClass: ''})
    else
      this.setState({selectedCategory: null, selectedClass: 'active'})
  
}

});


