var React = require('react')
var Link = require('react-router').Link

var JobsAction = require('../actions/jobs-actions.js')
var JobsStore = require('../stores/jobs-store.js')

module.exports = React.createClass({
	getInitialState:function(){
		return JobsStore.getState()
	},
	componentWillMount:function(){
		JobsStore.listen(this.onChange)
		JobsAction.loadJobs()
	},
	
	componentWillUnmount:function(){
		JobsStore.unlisten(this.onChange)
	},
	render: function() {
		var selectCategory = this.props.selectCategory
		return (
			<div className="col-md-9">
			<table className="table table-bordered table-stripped">
			<thead>
			<tr>
			<th>Location</th><th>Position</th><th>Company</th>
			</tr>
			</thead>
			<tbody>
			{selectCategory ? this.renderFilteredJobs() : this.renderJobs()}
			</tbody>
			</table>
			</div>
			)
	},
	onChange:function(state){
		this.setState(state)
	},
	renderJobs:function(){
		
		return this.state.jobs.map(function(job){
			return(
				<tr key={job.id}>
				<td><Link to={"/job/"+job.id}>{job.location}</Link></td>
			<td>{job.position}</td>
			<td>{job.company}</td>
			</tr>
			)
		})

	},
	renderFilteredJobs: function(){
		var selectCategory = this.props.selectCategory
		var jobs = this.state.jobs.filter(function(job){
			return job.categoryid === selectCategory

		})

		return jobs.map(function(job){
			return(
				<tr key={job.id}>
				<td><Link to={"/job/"+job.id}>{job.location}</Link></td>
			<td>{job.position}</td>
			<td>{job.company}</td>
			</tr>
			)
		})
	}
})


