'use strict';

module.exports = {
	getToken:function() {
		return JSON.parse(localStorage.getItem('token'))
	},
	setToken:function(token) {
		if (token)
			localStorage.setItem('token', JSON.stringify(token))
		else
			localStorage.removeItem('token')
	}
	
	
};


