var React = require('react')

var JobPanelActions = require('../actions/job-panel-actions.js')

module.exports = React.createClass({
	
	render: function(){
			return (
			<li onClick={this.selectCategory} ><a className="btn">{this.props.item.name}</a></li>
			)
	},
	selectCategory: function(){
		//console.log(this.props.item.id)
		JobPanelActions.selectCategory(this.props.item.id)
	}


	})


