var alt = require('../libs/alt-application')
var AuthToken = require('../libs/auth-token')

var axios = require('axios')

var JobCreateActions = require('../actions/job-create-actions.js')

module.exports = alt.createStore({
  displayName: 'JobCreateStore',

  bindListeners: {
    create: JobCreateActions.create,
    loadCategories: JobCreateActions.loadCategories,
    setValue: JobCreateActions.setValue
  
  },

  state: {
    job: {},
    created: false,
    categories: [],
    selectValue: null,
    created: null
  },
  
  create: function (postdata) { 
  var token = AuthToken.getToken()
  var postoken = "Bearer " + token
  axios({url: 'http://localhost:3000/jobs/', method: 'post',  data: postdata, headers:{'Authorization': postoken}}).then(function(resp){
  console.log(resp.data)
  this.setState({created: true})
  }.bind(this))

},

loadCategories: function() {
  axios.get('http://localhost:3000/api/category/').then(function(resp) {
    this.setState({categories: resp.data, created: null})

  }.bind(this))
},
setValue: function(value) {
  this.setState({setValue: value, created:null})
}
});


