var alt = require('../libs/alt-application.js')
var axios = require('axios')

var RegisterActions = require('../actions/register-actions.js')


module.exports = alt.createStore({
  displayName: 'RegisterStore',

  bindListeners: {
      register: RegisterActions.register
  },

  state: {
    registered: false
  },
  
 register: function(postdata) {
  axios.post('http://localhost:3000/users/register/', postdata).then(function(res){
this.setState({registered: true})
  }.bind(this))
}

});


