
var alt = require('../libs/alt-application.js')
var axios = require('axios')


var JobsActions = require('../actions/jobs-actions.js')

module.exports = alt.createStore({
  displayName: 'JobsStore',

  bindListeners: {
    loadJobs: JobsActions.loadJobs
  },

  state: {
    jobs: []
  },
  
  loadJobs: function () {
    axios.get('http://localhost:3000/api/jobs/').then(function(resp){
			this.setState({jobs:resp.data})
      }.bind(this))

  }
});



