var React = require('react')
var Link = require('react-router').Link

var JobDetailActions = require('../actions/job-detail-actions')
var JobDetailStore = require('../stores/job-detail-store')

module.exports = React.createClass({
	getInitialState: function(){
		return JobDetailStore.getState()
	},
	componentWillMount: function(){
		JobDetailStore.listen(this.onChange)
		this.loadDetail()
	},
	componentWillUnmount: function(){
		JobDetailStore.unlisten(this.onChange)
	},
	render: function(){
		var job = this.state.job
		return (
			<div className = " panel panel-info">
			<div className="panel-heading">
			<div className="panel-title">
			Job Detail
			</div>
			</div>
			<div className="panel-body">
			<p>{job.description}</p>
			<p>{job.location}</p>
			<p>{job.position}</p>
			</div>
			</div>
			)
	},
	onChange: function(state){
		this.setState(state)
	},
	loadDetail: function(){
		var id = this.props.params.id
		JobDetailActions.load(id)
	}
})


