var React = require('react')
var ReactDom = require('react-dom')
var Link = require('react-router').Link
var Navbar = require('react-bootstrap').Navbar

var HeaderStore = require('../stores/header-store.js')
var HeaderActions = require('../actions/header-actions.js')

module.exports = React.createClass({
	getInitialState: function(){
		return HeaderStore.getState()
	},
	componentWillMount: function(){
		HeaderStore.listen(this.onChange)
		
	},
	
	componentWillUnmount:function(){
		HeaderStore.unlisten(this.onChange)
	},
	
	render:function(){
		var user = this.state.user
				return (
					
<header>
{user ? this.renderAuth() : this.renderGuest()}
</header>
					
					 
					)
	},
	onChange:function(state) {
		this.setState(state)
	},
	logout: function(){
		HeaderActions.logout()
	},
	handleSubmit:function(e){
		e.preventDefault();
		
		var email = ReactDom.findDOMNode(this.refs.email).value.trim()
		var password = ReactDom.findDOMNode(this.refs.password).value.trim()
		
		if(!email || !password){
			return
		}
			
		// send to server
		
var postdata ={}
 postdata.email = email
 postdata.password = password
 HeaderActions.login(postdata)

		ReactDom.findDOMNode(this.refs.email).value='';
		ReactDom.findDOMNode(this.refs.password).value='';
		return;
	},
	renderAuth:function(){
		return(
				<Navbar inverse={true}> <Navbar.Header> <Navbar.Brand><a href="#/">Jobeet</a></Navbar.Brand> </Navbar.Header>
<div className="navbar-right">
<p className="navbar-text ">Hello {this.state.user}</p>
<Link className="btn btn-primary navbar-btn" to="/jobcreate">Post Jobs</Link>
{this.renderPostCategory()}
<button className="btn btn-success navbar-btn " onClick = {this.logout} type="button" >Logout</button>
</div>
</Navbar>

			)
	},
	renderGuest:function(){
		return(
			<Navbar inverse={true}> <Navbar.Header> <Navbar.Brand><a href="#/">Jobeet</a></Navbar.Brand> </Navbar.Header>
<ul className="nav navbar-nav navbar-right">
<li >
<Link to="/register">Register</Link>
</li>
</ul> <form className="navbar-form navbar-right" role="search"> <div className="form-group"> <input type="text" className="form-control" placeholder="your email"  ref="email"/> </div> <div className="form-group"> <input type="password" className="form-control" placeholder="your password"  ref="password"/> </div> <button onClick={this.handleSubmit} className="btn btn-primary">Login</button>  </form>

 </Navbar>

			)
	},

	renderPostCategory: function() {
		var userEmail = this.state.email
		if (userEmail === "egaleme@gmail.com") {
			return <Link className="btn btn-info navbar-btn" to="/categorycreate">Post Job Categories</Link>
		}
		return ""
	}


});


