var alt = require('../libs/alt-application')
var AuthToken = require('../libs/auth-token')

var axios = require('axios')

var CategoryActions = require('../actions/category-actions.js')

module.exports = alt.createStore({
  displayName: 'CategoryStore',

  bindListeners: {
    load: CategoryActions.load,
    create: CategoryActions.create
  
  },

  state: {
    categories: [],
    created: null
  },
  
  load: function () { 
  axios.get('http://localhost:3000/api/category/').then(function(res){
  this.setState({categories: res.data, created: null})
  
   }.bind(this))
  

},

create: function(postData) {
  var token = AuthToken.getToken()
  var postoken = "Bearer " + token
  axios({url: 'http://localhost:3000/category/', method: 'post',  data: postData, headers:{'Authorization': postoken}}).then(function(resp){
  this.setState({created: true})
  }.bind(this))
}
});


