var React = require('react')
var ReactDom = require('react-dom')
var Link = require('react-router').Link


var RegisterActions = require('../actions/register-actions.js')
var RegisterStore = require('../stores/register-store.js')

module.exports = React.createClass({
	getInitialState: function() {
		return RegisterStore.getState()
	},
	componentWillMount: function() {
		RegisterStore.listen(this.onChange)
	},
	componentWillUnmount: function() {
		RegisterStore.unlisten(this.onChange)
	},

	render: function() {
		var registered = this.state.registered
		return (
		<div>	
		{registered ? this.renderRegistered() : this.renderNotRegistered()}
		<form className="form-horizontal" role="form" onSubmit={this.handleSubmit}> 
		<div className="form-group">
		 <label htmlFor="firstname" className="col-sm-2 control-label">First Name</label> 
		 <div className="col-sm-10">
		  <input type="text" className="form-control" placeholder="Enter First Name" ref="firstname" /> </div> </div> 
		  <div className="form-group">
		  <label htmlFor="lastname" className="col-sm-2 control-label">Last Name</label>
		  <div className="col-sm-10">
		  <input type="text" className="form-control" placeholder="Enter Last Name" ref="lastname" /> </div> </div>
		  <div className="form-group">
		  <label htmlFor="email" className="col-sm-2 control-label">Email</label>
		  <div className="col-sm-10">
		  <input type="text" className="form-control" placeholder="Enter Your Email" ref="email" /> </div> </div>
		  <div className="form-group">
		 <label htmlFor="password" className="col-sm-2 control-label">Password</label> 
		  <div className="col-sm-10">
		  <input type="password" className="form-control"  placeholder="Enter Password" ref="password" /> </div> </div>
		  <div className="form-group"> <div className="col-sm-offset-2 col-sm-10"> <button type="submit" className="btn btn-default">Register</button> </div> </div>
		   </form>
		  </div>
			)
		
	},
	onChange: function(state){
		this.setState(state)
	},

	renderRegistered: function(){
		return <div className="alert alert-success">Success! Well done you are now registered.</div>

	},
	renderNotRegistered: function() {
		return <div> </div>
	},

	handleSubmit: function(e){
		e.preventDefault();
		
		var firstname = ReactDom.findDOMNode(this.refs.firstname).value.trim()
		var lastname = ReactDom.findDOMNode(this.refs.lastname).value.trim()
		var email = ReactDom.findDOMNode(this.refs.email).value.trim()
		var password = ReactDom.findDOMNode(this.refs.password).value.trim()
		
		if(!email || !password || !lastname || !firstname){
			return
		}
			
		// send to server
		
var postdata ={}
 postdata.firstname = firstname
 postdata.lastname = lastname
  postdata.password = password
  postdata.email = email
 RegisterActions.register(postdata)

		ReactDom.findDOMNode(this.refs.firstname).value='';
		ReactDom.findDOMNode(this.refs.lastname).value='';
		ReactDom.findDOMNode(this.refs.email).value='';
		ReactDom.findDOMNode(this.refs.password).value='';
		return;
	}
});


