var React = require('react')
var Router = require('react-router').Router
var Route = require('react-router').Route
var IndexRoute = require('react-router').IndexRoute
var Link = require('react-router').Link


var App = require('./app.jsx')
var Register = require('./register.jsx')
var JobPanel = require('./job-panel.jsx')
var JobDetail = require('./job-detail.jsx')
var JobCreate = require('./job-create')
var CategoryCreate = require('./category-create')



module.exports = (
  <Router>
    <Route path="/" component={App}>
    <Route path="jobs" component={JobPanel} />
    <IndexRoute component={JobPanel} />
    <Route path="job/:id"  component={JobDetail} />
    <Route path="jobcreate"  component={JobCreate} />
    <Route path="categorycreate" component={CategoryCreate} />
    <Route path="register" component={Register} />
    </Route>
  </Router>
);



