
var alt = require('../libs/alt-application.js')
var axios = require('axios')


var HeaderActions = require('../actions/header-actions.js')
var AuthToken =  require('../libs/auth-token.js')

module.exports = alt.createStore({
  displayName: 'HeaderStore',

  bindListeners: {
    login: HeaderActions.login,
    logout: HeaderActions.logout,
    register: HeaderActions.register,
    isLoggedIn: HeaderActions.isLoggedIn
  },

  state: {
    user: null,
    email: null
  },
  
  login: function (postdata) { 
  axios.post('http://localhost:3000/users/login/', postdata).then(function(res){
  this.setState({user: res.data.firstname, email: res.data.email})
  AuthToken.setToken(res.data.token)
 }.bind(this))
  
},
logout: function(){
  AuthToken.setToken()
  this.setState({user: null})
},
register: function(postdata) {
  axios.post('http://localhost:3000/users/register/', postdata).then(function(res){
console.log(res.data)
  }.bind(this))
},

isLoggedIn: function() {
  if (AuthToken.getToken())
      return true;
  else
      return false;
}

});


