
var alt = require('../libs/alt-application.js')
var axios = require('axios')

var JobDetailActions = require('../actions/job-detail-actions.js')

module.exports = alt.createStore({
  displayName: 'JobDetailStore',

  bindListeners: {
    load: JobDetailActions.load
    
  },

  state: {
   job: {}
   
  },
  
  load: function(id){
   axios.get('http://localhost:3000/api/jobs/'+id).then(function(resp){
    this.setState({job: resp.data})
   }.bind(this))
  
}

});


